import React, { useState } from 'react';
import people from './data';
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from 'react-icons/fa';

const Review = () => {
   const [index,setIndex] = useState(0);
   const { id, name, job, image, text} = people[index];

   const checkIndex = (n = 0) => {
      if (n > people.length - 1) {
         return 0;
      } else if (n < 0 ) {
         return people.length - 1;
      } else
      return n;
   }

   const nextUser = () => {
      let newIndex = index + 1;
      setIndex(checkIndex(newIndex));
   }

   const prevUser = () => {
      let newIndex = index - 1;
      setIndex(checkIndex(newIndex));
   }

   const randomUser = () => {
      let newPerson = Math.floor(Math.random()* people.length);
      if ( index != newPerson) {
      setIndex(newPerson);
       } else {
         setIndex(checkIndex(newPerson - 1));
       }
   }

  return ( 
  <article className='review'>
     <div className='img-container'>
      <img className='person-img' src={image} alt={name}/>
      <span className='quote-icon'>
         <FaQuoteRight />
      </span>
     </div>
     <h4 className='author'>{name}</h4>
     <p className='job'>{job}</p>
     <p className='info'>{text}</p>
     <div>
        <button className='prev-next-btn' onClick={prevUser}><FaChevronLeft /></button>
        <button className='prev-next-btn' onClick={nextUser}><FaChevronRight /></button>
     </div>
     <button className='random-btn' onClick={randomUser}>surprise me</button>
  </article>
  );
};

export default Review;
